* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: black;
    padding: 70px;
}

a {
    text-decoration: none;
}

main {
    background-color: whitesmoke;
    height: 400px;
    max-width: 100%;
    border-radius: 5px;
    /* margin-top: 80px; */
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.title {
    font-size: 40px;
    margin: 0;
}

.instructions {
    margin-bottom: 20px;
}

.dice-container {
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    gap: 20px;
    margin-bottom: 40px;
}

.die-face {
    height: 50px;
    width: 50px;
    box-shadow: 0 2px 2px  rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.die-num {
    font-size: 1.5rem;
}

.roll-dice {
    height: 50px;
    width: 100px;
    border: none;
    border-radius: 6px;
    background-color: rebeccapurple;
    color: white;
    font-size: 1rem;
    cursor: pointer;
}

.roll-dice:focus {
    outline: none;
}

.roll-dice:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.count {
    height: 50px;
    width: 100px;
    padding: 15px;
    border: none;
    border-radius: 6px;
    background-color: rebeccapurple;
    color: white;
    margin-left: 10px;
}

.timer {
    height: 50px;
    width: 100px;
    padding: 15px;
    border: none;
    border-radius: 6px;
    background-color: rebeccapurple;
    color: white;
    margin-left: 10px;
}

@media screen and (max-width: 800px) {

    main {
        height: 85vh;
    }

    .die-face {
       height: 33px;
       width: 33px;
    }

    .die-num {
        font-size: 1rem;
    }
    
    body {
        padding: 20px;
    }

    .roll-dice {
        height: 50px;
        width: 60px;
    }

    

  }